.card-link{
    width: 100%;

    @media screen and (min-width: 576px) {
        width: 45%;
        margin: 5px 2.5%; 
    }

    @media screen and (min-width: 992px) {
        width: 30%;
        margin: 5px 1.6%; 
    }
    .card {
        position: relative;
        width: 100%;
        height: 350px;
        margin-top: 25px;
        margin-bottom: 15px;
        border-radius: 8px;
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
        overflow: hidden;
        transition: all 0.3s ease-in-out;
        &:hover {
            transform: scale(1.05);
        }
    
        img {
            height: 100%;
            width: 100%;
            object-fit: cover;
            filter: brightness(75%);
        }
        p {
            position: absolute;
            bottom: 20px;
            left: 20px;
    
    
            
            z-index: 1000;
            color: rgb(255, 255, 255);
           
            font-family: Montserrat;
            font-size: 18px;
            font-weight: 700;
            line-height: 25.67px;
    
    
            
            
        }
    
    
    }
}
